<template>
  <div class="card">
    <div class="card-header">Exam03ReactiveData</div>
    <div class="card-body">
      <p>번호:{{ product.no }}</p>
      <p>이름:{{ product.name }}</p>
      <p>회사:{{ product.company }}</p>
      <p>가격:{{ getPrice() }}</p>
      <p>판매:{{ product.detail.sale ? "판매" : "품절" }}</p>
      <hr />
      <p v-html="product.detail.info"></p>
      <hr />
      <p>
        <img src="@/assets/products/minibag-black.png" class="mr-2" height="150" />
        <img v-bind:src="require(`@/assets/products/${product.image}`)" height="150" />
        <span v-if="product.detail.sale"><img src="@/assets/products/soldout.jpg" width="100" /></span
        ><!--v-if가 display none같은 효과를 준다-->
        <span v-show="product.detail.sale"><img src="@/assets/products/soldout.jpg" width="100" /></span
        ><!--v-로 시작하는 속성값은 무조건 자바스크립트 코드-->
      </p>
      <hr />
      <button @click="changeData" class="btn btn-info btn-sm">데이터 변경</button>
    </div>
  </div>
</template>

<script setup>
//컴포넌트 속성 선언
import { reactive } from "vue";
//컴포넌트 반응형 속성 선언
let product = reactive({
  no: 1,
  name: "미니백",
  company: "클레인",
  price: 300000,
  image: "minibag-black.png",
  detail: {
    info: `<div>시그너츠 Cecyle Lock 마그네틱 클로저가 특징입니다.</div>`,
    sale: false,
  },
});

function getPrice() {
  return product.price;
}
function changeData() {
  //반응형 객체의 내부 속성값 변경
  product.no++;
  product.name = "빨간 미니백";
  product.price += 10000;
  product.image = "minibag-red.png";

  product.detail.info = "<div>시그너츠 Cecyle Lock 마그네틱 클로저가 특징입니다.</div>";
  product.detail.sale = !product.detail.sale;
  //객체 변경은 불가능
}
</script>

<style scoped></style>
